"use strict";
boann.controller('LoginController', ['$scope', '$http', '$window', '$state', function($scope, $http, $window, $state) {
    
    var URI     =   controler.view + "login/index.php";

    /* $scope.submit = function(data){
        console.log(URI);
    } */
 
    $scope.submit = function(data){
        if(data){
            var VALUE = [{data:data}];
            $http.post(URI, VALUE, {params:{action:"userLogin"}}).then(function(data){
                console.log(data.data);
                switch(data.data.data){
                    case "error" :
                        swal("Oeps!", data.data.dataContent, "error");
                    break;
                    
                    case "success" :
                        //location.href = data.data.rederectUrl;
                        location.reload();
                    break;
                }
            });  
        };
    }

}]);
