"use strict";
boann.controller('blogController', ['$scope', '$http', '$window', '$state', function($scope, $http, $window, $state) {
    
    console.log($state.$current.url.pattern.split("/")[1] + " = case name");
    console.log($state.router.globals.$current.views.mainpage.controller + "Controller is Loaded");

    var URI     =   controler.view + "blog/index.php";
    var state   =   $state.$current.url.pattern.split("/")[1];  


    switch(state){
        case "blog-new" : 
                getCatogory("getCatogory");                  
                getTags("getTags");                  
                
                $scope.submit = function(data){
                    if(data){
                        //do somting
                        postContent(data, "postBlog", "");
                        //go to 
                    }
                }

                $scope.tinymceOptions = {
                    height : "550",
                    plugins: 'link image code',
                    toolbar: 'undo redo | bold italic | alignleft aligncenter alignright | code'
                }; 
        break;

        case "blog-pages" :                   
                getContent("getBlog");

                $scope.view = function(data){
                    $state.go("blog-view", {uuid:data.uuid});                    
                };
                
                $scope.edit = function(data){
                    $state.go("blog-update", {uuid:data.uuid});                      
                };

                $scope.remove = function(data){
                    if(data){
                        swal({
                            icon:   "info",
                            title:  "Weet je zeker dat je dit item wil verwijderen ?",
                            showCancelButton: true,
                            buttons: [true, "Delete it!"],                                                      
                        }).then(function(value){
                            if(value){
                                //delete 
                                if(data){
                                    //do somting
                                    var VALUE = [{data:data}];
                                    
                                     
                                    $http.post(URI, VALUE, {params:{action:"removeBlog"}}).then(function(data){
                                        console.log(data);
                                    });
                                    
                                    //postContent(data, "removeBlog", "");

                                    //go to 
                                    //swal("Seccess!", "Item is removed!", "success");
                                    //getContent("getBlog");
                                }                              

                            }
                        });
                    }else{
                        //show error
                        swal("Oeps!", "No data found!", "error");
                    }
                };

        break;

        case "blog-catogory" :            
            getContent("getCatogory");
            
            $scope.addCatogory  = function(data){
                if(data){
                    postContent(data, "postCatogory", "getCatogory");
                }
            }        
        break;

        case "blog-tags" : 
            getContent("getTags");

            $scope.addTag  = function(data){
                if(data){
                    postContent(data, "postTags", "getTags");
                }
            }                  
        break;

        case "blog-reacties" :
            getContent("getReacties");
            $scope.addCatogory  = function(data){
                if(data){
                    postContent(data, "postReacties");
                }
            }                    
        break;        
    }


            function getTags(location){
                $http.get(URI, {params:{action:location}}).then(function(data){
                    if(data.status === 200){
                        $scope.tags = data.data                                               
                    };
                });
            }

            function getCatogory(location){
                $http.get(URI, {params:{action:location}}).then(function(data){
                    if(data.status === 200){
                        $scope.catogory = data.data                                               
                    };
                });
            }

            function getContent(location){
                $http.get(URI, {params:{action:location}}).then(function(data){
                    if(data.status === 200){
                        $scope.items = data.data
                        console.log(data.data);
                    };
                });
            }

            function postContent(data, location, reloadLocation){                    
                var VALUE = [{data:data}];
                $http.post(URI, VALUE, {params:{action:location}}).then(function(data){
                        console.log(data.data);
                    if(data.status === 200){
                        switch(data.data.data){
                            case "error" :
                                swal("Oeps!", data.data.dataContent, "error");
                            break;

                            case "success" :
                                swal("Well done", data.data.dataContent, "success");
                                getContent(reloadLocation);
                            break;
                        }
                    };
                });
            }

}]);